.header {
  position: sticky;
  top: 0;

  display: flex;
  padding: 10px;
  margin-bottom: 20px;

  background-color: #7B59CE;

  z-index: 2;
}

.link {
  display: block;

  font-size: 22px;
  font-weight: 500;
  color: #fff;

  padding: 2px 6px;

  border: 2px solid #fff;
}
